//     
//Chart Components
//Detail  

import { useQuery } from "react-query";
import styled from "styled-components";
import { getCoin_chartData } from "../../modules/fetchs";
import { useParams } from "react-router-dom";
import ReactApexChart from "react-apexcharts";
import { useRecoilValue } from "recoil";
import { isDarkTheme } from "../../modules/atoms";
import { Helmet } from "react-helmet-async";

interface I_Chart {
    coinNm?: string;
    coinSymbol?: string;
}

interface I_ChartData {
    time_open?: number; 
    time_close?: number; 
    open?: string; 
    high?: string; 
    low?: string; 
    close?: string; 
    volume?: string; 
    market_cap?: number;
};

const ChartBox = styled.div`
    margin-top: 15px;
`;

const ChartTitles = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    img {
        width: 25px;
        height: 25px;
        margin-right: 10px;
    }
`;

const ChartBodys = styled.div`
    margin-top: 5px;
`;

function Chart({coinNm, coinSymbol}: I_Chart){
    const {coinID} = useParams();

    const {isLoading: chartLoading, data: CoinChart} = useQuery<I_ChartData[]>({
        queryKey: "CoinChartData",
        queryFn: () => getCoin_chartData(coinID)
    });

    const isDarks = useRecoilValue(isDarkTheme);

    console.log(CoinChart);

    return (
        <>
            <Helmet>
                <title>{coinSymbol}/Chart</title>
            </Helmet>
            {
                chartLoading ? `${coinID}    .`
                : (
                    <ChartBox>
                        <ChartTitles>
                            <img src={`https://static.coinpaprika.com/coin/${coinID}/logo.png`}/>
                            <h4>{coinNm} / {coinSymbol} Chart</h4>
                        </ChartTitles>
                        <ChartBodys>
                            <ReactApexChart 
                                type="candlestick"
                                series={[
                                    {
                                        name: "Price",
                                        data: CoinChart?.map((price) => ({
                                            x: new Date(price?.time_open as any),
                                            y: [
                                                parseFloat(String(price?.open)),
                                                parseFloat(String(price?.high)),
                                                parseFloat(String(price?.low)),
                                                parseFloat(String(price.close))
                                            ]
                                        })) as any[]
                                    }
                                ]}
                                options={{
                                    plotOptions: {
                                        candlestick: {
                                            colors: {
                                                upward: "#ff4757",
                                                downward: "#5352ed"
                                            }
                                        }
                                    },
                                    theme: {
                                        mode: isDarks ? "dark" : "light"
                                    },
                                    chart: {
                                        width: "100%"
                                    },
                                    xaxis: {
                                        type: "datetime",
                                        categories: CoinChart?.map(
                                            (price) => (price.time_close)
                                        ),
                                        labels: {
                                            datetimeFormatter: {
                                                month: "mmm 'yy"
                                            }
                                        }
                                    }
                                }}
                            />    
                        </ChartBodys>                
                    </ChartBox>
                )
            }
        </>
    );
}

export default Chart;